import React from "react";
import { SocialIcons, MenuLinks } from "../CommonStyles/styles";
import gmail from "../../assets/img/gmail.svg";
import linkedin from "../../assets/img/linkedin.svg";
import portfolio from "../../assets/img/portfoliotwo.svg";

const Header = () => {
  return (
    <>
      <div className="d-flex justify-content-between align-items-center f1 bg-light">
        <div className="d-flex">
          <MenuLinks>
            <img />
            <span className="menuTxt">About Me</span>
          </MenuLinks>
          <MenuLinks>
            <img />
            <span className="menuTxt">My Works</span>
          </MenuLinks>
          <MenuLinks>
            <img />
            <span className="menuTxt">Skill Set</span>
          </MenuLinks>
          <MenuLinks className="mview">
            <img />
            <span className="menuTxt">Resume</span>
          </MenuLinks>
        </div>
        <div className="d-flex">
          <SocialIcons>
            <img src={linkedin} className="menuImg" />
          </SocialIcons>
          <SocialIcons>
            <img src={gmail} className="menuImg" />
          </SocialIcons>
        </div>
      </div>
    </>
  );
};

export default Header;
