import React from "react";
import { Heading, LightText } from "../CommonStyles/styles";

const About = () => {
  return (
    <div className="row f1" style={{ height: "80vh" }}>
      <div className="col-lg-12 col-xs-12">
        <Heading size={3} className="f1">
          About Me
        </Heading>

        <blockquote class="blockquote">
          <p class="mb-0">
            Solving critical problems using the idea of <b>“ reasoning from first principles ” </b>
            is way more exciting than anything for me . As humans that's what we have always done.
          </p>
          <br />
          <footer class="blockquote-footer">Myself</footer>
        </blockquote>
      </div>
    </div>
  );
};

export default About;
