import React from "react";
import { Heading, LightText } from "../CommonStyles/styles";
import ProgressBar from "../common/ProgressBar";

const Skills = () => {
  return (
    <>
      <Heading size={2} className="f1">
        Skills
      </Heading>{" "}
      <div className="row f1">
        <div className="col-lg-4 border-right">
          <blockquote class="blockquote">
            <p class="mb-0">
              " My primary skill is the ingenuity in creating sustainable software products "
            </p>
            <br />
          </blockquote>
        </div>
        <div className="col-lg-8 col-xs-12">
          <div className="row m-0 align-items-center">
            <div className="col p-2">HTML,CSS</div>
            <div className="col-8 p-2">
              <ProgressBar value={75} />
            </div>
            <div className="col p-2 text-center d-none d-sm-block">75%</div>
          </div>
          <div className="row m-0 align-items-center">
            <div className="col p-2">Javascript</div>
            <div className="col-8 p-2">
              <ProgressBar value={80} />
            </div>
            <div className="col p-2 text-center d-none d-sm-block">80%</div>
          </div>{" "}
          <div className="row m-0 align-items-center">
            <div className="col p-2">ReactJS</div>
            <div className="col-8 p-2">
              <ProgressBar value={78} />
            </div>
            <div className="col p-2 text-center d-none d-sm-block">78%</div>
          </div>{" "}
          <div className="row m-0 align-items-center">
            <div className="col p-2">Angular JS, Angular</div>
            <div className="col-8 p-2">
              <ProgressBar value={45} />
            </div>
            <div className="col p-2 text-center d-none d-sm-block">45%</div>
          </div>{" "}
          <div className="row m-0 align-items-center">
            <div className="col p-2">PHP, Node JS, Java</div>
            <div className="col-8 p-2">
              <ProgressBar value={65} />
            </div>
            <div className="col p-2 text-center d-none d-sm-block">65%</div>
          </div>{" "}
          <div className="row m-0 align-items-center">
            <div className="col p-2">Mysql, MongoDB</div>
            <div className="col-8 p-2">
              <ProgressBar value={52} />
            </div>
            <div className="col p-2 text-center d-none d-sm-block">52%</div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Skills;
