import React from "react";
import { HomeContainer, Heading, HeaderImg, LightText, GrayText } from "../CommonStyles/styles";
import Header from "../Header";
import profileImg from "../../assets/img/profile.jpg";
import Skills from "../Skills";
import About from "../About";

const Home = () => {
  return (
    <HomeContainer>
      <Header />
      <div className="container">
        <div
          className="d-flex justify-content-center align-items-center"
          style={{ height: "80vh" }}
        >
          <div className="row">
            <div className="col-lg-6 col-xs-12">
              <div className="d-flex p-2 justify-content-around align-items-center">
                <div className="f1 flex-grow-10">
                  <HeaderImg>
                    <img src={profileImg} className="rounded-circle" />
                  </HeaderImg>
                </div>
              </div>
            </div>
            <div className="col-lg-6 col-xs-12">
              <div className="d-flex p-2 justify-content-around align-items-center">
                <div className="f1 flex-grow-10">
                  <Heading size={5}>Hello, I'm Saravanan</Heading>
                  <LightText className="lead">Front-end Developer and Project Lead</LightText>
                  <GrayText></GrayText>
                </div>
              </div>
            </div>
          </div>
        </div>{" "}
        <hr></hr>
        <Skills />
        <hr></hr>
        <About />
      </div>
    </HomeContainer>
  );
};

export default Home;
